body{
  padding: 30px;
}
input{
  color: grey;
  font-size: 1.2rem;
  width: 50%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow:hidden;
  text-align: center;
}
.card p{
  padding: 5px;
}
.card img{
  max-height: 170px;
  padding-top: 20px;
  
}
h2{
  font-size: 2.3rem;
  text-align: center;
};

